.Projet {
  z-index: 8;
  position: absolute;
  width: 65%;
  height: 100%;
  display: flex;
  justify-content: center;
  overflow: hidden;

  @media screen and (max-width: 768px) {
    width: 85%;
  }

  .header-image-container {
    border-radius: 20px;
    width: 100%;
    position: absolute;
    height: 75vh;
    top: -60vh;
    overflow: hidden;

    @media screen and (max-width: 768px) {
      display: none;
    }

    .header-image {
      object-fit: cover;
      object-position: center;
      width: 110%;
    }
  }

  .projet-content {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-direction: column;
    margin-top: 12%;
    overflow-y: scroll;
    border-radius: 20px 20px 0px 0px;
    -ms-overflow-style: none;
    scrollbar-width: none;

    @media screen and (max-width: 768px) {
      margin-top: 20%;
    }

    .projet-content-header {
      margin-top: 2em;
      display: flex;
      justify-content: space-between;
      align-items: center;

      h2 {
        font-family: "Newake";
        font-size: 3.5em;
        margin: 0;
        text-transform: uppercase;

        @media screen and (max-width: 768px) {
          font-size: 2.5em;
          justify-content: center;

          &:last-child {
            font-size: 1.5em;
          }
        }
      }
    }

    .projet-content-techno {
      display: flex;
      justify-content: start;
      width: 100%;
      gap: 1em;

      @media screen and (max-width: 768px) {
        gap: 0.5em;
      }

      span {
        font-family: "Soulcraft";
        font-size: 1.2em;
        color: white;
        padding: 0.5em 1em;
        border-radius: 10px;
        margin: 10px 0;

        @media screen and (max-width: 768px) {
          font-size: 0.8em;
          margin: 5px 0;
        }
      }
    }

    .projet-content-description {
      font-family: "Satoshi";
      text-align: justify;
      font-size: 1.2em;
      line-height: 1.5em;
      margin-block: 0;
    }

    .projet-images-container {
      display: flex;
      flex-direction: column;
      margin-top: 1em;
      width: 100%;

      img {
        margin-top: 1em;
        width: 100%;
        border-radius: 10px;
      }

      video {
        margin-top: 1em;
        width: 100%;
        border-radius: 10px;
      }
    }
  }
}
